.main {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.mainTop {
  margin: 90px 0 0 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.popup {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 720px;
  min-height: 539px;
  background-color: #1c1c21;
  border-radius: 40px;
  z-index: 3;
}

.head {
  padding: 0;
  min-width: 640px;
  min-height: 64px;
  display: flex;
  justify-content: space-between;
}

.headCenter {
  padding: 0;
  min-width: 640px;
  min-height: 64px;
  display: flex;
  justify-content: center;
}

.img {
  margin: 0;
  padding: 0;
  min-width: 480px;
}

.details {
  padding: 0;
  display: grid;
  grid-template-columns: repeat(4, fit-content(120px));
  grid-template-rows: repeat(1, fit-content(56px));
  gap: 20px;
}

.callories {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 120px;
  min-height: 56px;
  color: #8585ad;
}
