.popup {
  margin: 0;
  padding: 0;
  position: relative;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 720px;
  background-color: #1c1c21;
  border-radius: 40px;
  z-index: 8;
}

.close {
  position: absolute;
  top: 40px;
  right: 40px;
  display: flex;
  z-index: 9;
  cursor: pointer;
}
