.header {
  margin: 0 auto;
  padding: 0;
  max-width: 1280px;
  min-height: 88px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header::before {
  content: "";
  display: flex;
  width: 100%;
  height: 88px;
  background-color: #1c1c21;
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
}
